@import url(https://fonts.googleapis.com/css2?family=Lemonada:wght@300&display=swap);
:root {
  --primary: #c44569;
  --secondary: #485460;
  --back: #f8f7f7;
  --error: #ff4a4a;
}

body {
  font-family: "Lemonada";
  color: #485460;
  color: var(--secondary);
  background-color: #f8f7f7;
  background-color: var(--back);
}

.App {
  max-width: 960px;
  margin: 0 auto;
}

.title h1 {
  color: #c44569;
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}

.title h2,
p {
  text-align: center;
}

.title h2 {
  margin-top: 60px;
  font-size: 2.6rem;
}
.progress-bar {
  height: 5px;
  background: #c44569;
  background: var(--primary);
  margin-top: 20px;
}

form {
  margin: 30px auto 10px;
  text-align: center;
}

label input {
  height: 0;
  width: 0;
  opacity: 0;
}

label {
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid #c44569;
  border: 1px solid var(--primary);
  border-radius: 20%;
  margin: 10px auto;
  line-height: 30px;
  color: #485460;
  color: var(--secondary);
  font-size: 24px;
}
label:hover {
  background: #c44569;
  background: var(--primary);
  color: white;
  cursor: pointer;
}

.output {
  height: 60px;
  font-size: 0.8rem;
}
.error {
  color: #ff4a4a;
  color: var(--error);
}

.img-grid {
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 40px;
}
.img-wrap {
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  position: relative;
  opacity: 0.8;
}

.img-wrap img{
 min-width: 100%;
 min-height: 100%;
 max-width: 150%;
 position: absolute;
 top: 0;
 left: 0;
}

.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.backdrop img{
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white; 
}
